body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  padding: 5px 0 0; }

.App {
  text-align: center; }

.FancyTabs-tablist {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .FancyTabs-tablistItem {
    display: inline-block; }
    .FancyTabs-tablistItem + .FancyTabs-tablistItem {
      margin-left: 0.3em; }

.FancyTabs-panel {
  border-top: 5px solid #2697d1;
  padding: 1em;
  position: relative;
  z-index: 2;
  background: #fff;
  overflow: hidden; }

.FancyTabs-tab {
  cursor: pointer; }
  .FancyTabs-tab:focus {
    outline: 0; }
  .FancyTabs-tabInner {
    border: 1px solid transparent;
    border-bottom: 0;
    position: relative;
    padding: 15px 20px;
    background: #ccc;
    position: relative;
    -webkit-transition: background 0.2s linear, -webkit-transform 0.2s ease-out;
    transition: background 0.2s linear, -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out, background 0.2s linear;
    transition: transform 0.2s ease-out, background 0.2s linear, -webkit-transform 0.2s ease-out;
    -webkit-transition: -webkit-transform 0.2s ease-out, background 0.2s linear;
    transform: translateY(10px);
    -webkit-transform: translateY(10px); }
    .FancyTabs-tabInner.is-active {
      background: #2697d1;
      border-color: #41d0cb;
      border-width: 1px;
      color: #fff;
      text-decoration: none;
      transform: translateY(0);
      -webkit-transform: translateY(0); }
      .FancyTabs-tabInner.is-active::after {
        content: "";
        background: #fff;
        height: 1px;
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0; }

.FancyTabs-panelInner {
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease; }
  .FancyTabs-panelInner.is-enter {
    -webkit-transform: translateY(-120%);
            transform: translateY(-120%); }
  .FancyTabs-panelInner.is-enter-active, .FancyTabs-panelInner.is-leave {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  .FancyTabs-panelInner.is-leave-active {
    -webkit-transform: translateY(120%);
            transform: translateY(120%); }

